<template>
  <div class="text-center ma-3">
    <v-dialog v-model="dialog1" width="700">
      <template v-slot:activator="{on, attrs}">
        <v-btn color="grey lighten-5" v-bind="attrs" v-on="on">
          Nález psa
        </v-btn>
      </template>

      <v-stepper v-model="e1">
        <v-stepper-header class="px-16">
          <v-stepper-step :complete="e1 > 1" step="1" class="px-16" color="grey"></v-stepper-step>
          <v-divider></v-divider>
          <v-stepper-step step="2" class="px-16" color="grey"></v-stepper-step>
        </v-stepper-header>
        <v-stepper-items>
          <v-stepper-content step="1">
            <v-form action='/upload' method="POST" enctype="multipart/form-data" class="grey lighten-2">
              <v-container>
                <v-row class="mx-3 pa-6">
                  <v-col cols="6" class="py-0 px-5">
                    <v-menu
                      v-model="menu1"
                      :close-on-content-click="false"
                      :nudge-right="40"
                      transition="scale-transition"
                      offset-y
                      min-width="auto"
                    >
                      <template v-slot:activator="{ on, attrs }">
                        <v-text-field 
                          v-model="dateFormatted" 
                          label="Datum nálezu" 
                          prepend-inner-icon="mdi-calendar" 
                          readonly
                          required
                          v-bind="attrs"
                          v-on="on">
                        </v-text-field>
                      </template>
                      <v-date-picker 
                        v-model="date" 
                        @input="menu1 = false"
                        locale="cs-CZ"
                        :first-day-of-week="1"
                        :allowed-dates="(date) => date <= new Date().toISOString().substr(0, 10)">
                      </v-date-picker>
                    </v-menu>
                  </v-col>
                  <v-col cols="6" class="py-0 px-5">
                    <v-text-field 
                      v-model="foundDog.place"
                      label="Místo nálezu"
                      prepend-inner-icon="mdi-map-marker"
                      required>
                    </v-text-field>
                  </v-col>
                  <v-col cols="6" class="py-0 px-5">
                    <v-select
                      v-model="foundDog.sex"
                      :items="sexes"
                      label="Pohlaví">
                    </v-select>
                  </v-col>
                  <v-col cols="6" class="py-0 px-5">
                    <v-select
                      v-model="foundDog.breed"
                      :items="breeds"
                      label="Plemeno">
                    </v-select>
                  </v-col>
                  <v-col cols="12" class="py-0 px-5">
                    <v-text-field 
                      v-model="foundDog.size"
                      label="Velikost"
                      required>
                    </v-text-field>
                  </v-col>
                  <v-col>
                    <v-file-input 
                      v-model="file"
                      label="Select image file">
                    </v-file-input>
                    <!-- <input type="file" ref="file" name="file" @change="selectFile" > -->
                  </v-col>
                </v-row>
              </v-container>
              <v-btn color="grey-lighten-4" @click="e1 = 2">Dále</v-btn>
            </v-form>
          </v-stepper-content>
          <v-stepper-content step="2">
            <v-form class="grey lighten-2">
              <v-container>
                <v-row class="mx-3 pa-6">
                  <v-col cols="6" class="py-0 px-5">
                    <v-text-field 
                      v-model="foundDog.email"
                      label="Kontaktní e-mail"
                      prepend-icon="mdi-email"
                      required>
                    </v-text-field>
                  </v-col>
                  <v-col cols="12" class="py-0 px-5">
                    <v-textarea 
                      v-model="foundDog.info"
                      label="Dodatečné info">
                    </v-textarea>
                  </v-col>
                </v-row>
              </v-container>
              <v-btn color="grey-lighten-4" @click="e1 = 1">Zpět</v-btn>
              <v-btn text @click="newFoundDog(); dialog1 = false">Přidat</v-btn>
            </v-form>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-dialog>

    <v-dialog v-model="dialog2" width="700">
      <template v-slot:activator="{on, attrs}">
        <v-btn color="grey lighten-5" v-bind="attrs" v-on="on">
          Ztráta psa
        </v-btn>
      </template>

      <v-stepper v-model="e1">
        <v-stepper-header class="px-16">
          <v-stepper-step :complete="e1 > 1" step="1" class="px-16" color="grey"></v-stepper-step>
          <v-divider></v-divider>
          <v-stepper-step step="2" class="px-16" color="grey"></v-stepper-step>
        </v-stepper-header>
        <v-stepper-items>
          <v-stepper-content step="1">
            <v-form action='/upload' method="POST" enctype="multipart/form-data" class="grey lighten-2">
              <v-container>
                <v-row class="mx-3 pa-6">
                  <v-col cols="12" class="py-0 px-5">
                    <v-text-field 
                      v-model="lostDog.name"
                      label="Jméno"
                      required>
                    </v-text-field>
                  </v-col>
                  <v-col cols="6" class="py-0 px-5">
                    <v-menu
                      v-model="menu2"
                      :close-on-content-click="false"
                      :nudge-right="40"
                      transition="scale-transition"
                      offset-y
                      min-width="auto"
                    >
                      <template v-slot:activator="{ on, attrs }">
                        <v-text-field 
                          v-model="dateFormatted" 
                          label="Datum zmizení" 
                          prepend-inner-icon="mdi-calendar" 
                          readonly
                          required
                          v-bind="attrs"
                          v-on="on">
                        </v-text-field>
                      </template>
                      <v-date-picker 
                        v-model="date" 
                        @input="menu2 = false"
                        locale="cs-CZ"
                        :first-day-of-week="1"
                        :allowed-dates="(date) => date <= new Date().toISOString().substr(0, 10)">
                      </v-date-picker>
                    </v-menu>
                  </v-col>
                  <v-col cols="6" class="py-0 px-5">
                    <v-text-field
                      v-model="lostDog.place"
                      label="Místo zmizení"
                      prepend-inner-icon="mdi-map-marker"
                      required>
                    </v-text-field>
                  </v-col>
                  <v-col cols="6" class="py-0 px-5">
                    <v-select
                      v-model="lostDog.sex"
                      :items="sexes"
                      label="Pohlaví"
                      required>
                    </v-select>
                  </v-col>
                  <v-col cols="12" class="py-0 px-5">
                    <v-select 
                      v-model="lostDog.breed"
                      :items="breeds"
                      label="Plemeno"
                      required>
                    </v-select>
                  </v-col>
                  <v-col cols="12" class="py-0 px-5">
                    <v-text-field 
                      v-model="lostDog.size"
                      label="Velikost"
                      required>
                    </v-text-field>
                  </v-col>
                  <v-col cols="12" class="py-0 px-5">
                    <v-text-field 
                      v-model="lostDog.age"
                      label="Věk"
                      required>
                    </v-text-field>
                  </v-col>
                  <v-col>
                    <v-file-input 
                      v-model="file"
                      label="Select image file">
                    </v-file-input>
                    <!-- <input type="file" ref="file" name="file" @change="selectFile" > -->
                  </v-col>
                </v-row>
              </v-container>
              <v-btn color="grey-lighten-4" @click="e1 = 2">Dále</v-btn>
            </v-form>
          </v-stepper-content>
          <v-stepper-content step="2">
            <v-form class="grey lighten-2">
              <v-container>
                <v-row class="mx-3 pa-6">
                  <v-col cols="6" class="py-0 px-5">
                    <v-text-field 
                      v-model="lostDog.email"
                      label="Kontaktní e-mail"
                      prepend-icon="mdi-email"
                      required>
                    </v-text-field>
                  </v-col>
                  <v-col cols="12" class="py-0 px-5">
                    <v-textarea 
                      v-model="lostDog.info"
                      label="Dodatečné info">
                    </v-textarea>
                  </v-col>
                </v-row>
              </v-container>
              <v-btn color="grey-lighten-4" @click="e1 = 1">Zpět</v-btn>
              <v-btn text @click="newLostDog(); dialog2 = false">Přidat</v-btn>
            </v-form>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-dialog>
  </div>
</template>

<script>
import PostService from '../PostService';
import breeds from '../assets/breeds';

export default {
  data() {
    return {
      foundDog: {
        place: '',
        sex: '',
        breed: '',
        size: '',
        email: '',
        info: ''
      },
      lostDog: {
        name: '',
        place: '',
        sex: '',
        breed: '',
        size: '',
        age: '',
        email: '',
        info: ''
      },
      dialog1: false,
      dialog2: false,
      e1: 1,
      menu1: false,
      menu2: false,
      date: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0,10),
      file: [],
      breeds: breeds,
      sexes: ["pes", "fena"]
    }
  },

  computed: {
    dateFormatted() {
      return this.formatDate(this.date);
    },
    onlyPastDates() {
      console.log(this.date);
      return this.date;
    }
  },

  methods: {
    async newFoundDog() {
      const formData = new FormData();

      formData.append('status', 'found');
      formData.append('date', this.date);
      formData.append('place', this.foundDog.place);
      formData.append('sex', this.foundDog.sex);
      formData.append('breed', this.foundDog.breed);
      formData.append('size', this.foundDog.size);
      formData.append('email', this.foundDog.email);
      formData.append('info', this.foundDog.info);
      formData.append('file', this.file);

      await PostService.insertPost(
        formData
      );
      this.posts = await PostService.getPosts();
    },

    async newLostDog() {
      const formData = new FormData();

      formData.append('status', 'lost');
      formData.append('name', this.lostDog.name);
      formData.append('date', this.date);
      formData.append('place', this.lostDog.place);
      formData.append('sex', this.lostDog.sex);
      formData.append('breed', this.lostDog.breed);
      formData.append('size', this.lostDog.size);
      formData.append('age', this.lostDog.age);
      formData.append('email', this.lostDog.email);
      formData.append('info', this.lostDog.info);
      formData.append('file', this.file);

      await PostService.insertPost(
        formData
      );
      this.posts = await PostService.getPosts();
    },

    selectFile() {
      this.file = this.$refs.file.files[0];
    },

    formatDate(date) {
      if (!date) return null;

      const [year, month, day] = date.split('-');
      return `${day}. ${month}. ${year}`;
    }
  }
}
</script>